import React from "react";
import DropDown from "../../DropDown";
import image0 from "./insaneBolt0.png";
import image1 from "./insaneBolt1.png";
import image2 from "./insaneBolt2.png";

function Component() { 

    return <div>
        <h1>Insane Bolt</h1><br />
        <p>This is one of the miscellaneous challenges in the HTB Uni CTF Qualifiers 2021</p><br />
        <p>We are only given an ip address, a port number and the following description:</p>
        <div className="code" style={{textAlign: "center"}}>
            <code>
            {`This insane scientist wants to craft the most powerful android in the world! Help him collect many ðŸ”© to achieve his goal. Also, he needs many ðŸ’Ž to make it even more strong and pwoerful than any other android. Good luck adventurer!`}
            </code>
        </div>
        <p>First I tried connecting to the server using a tool called netcat.</p>
        <div className="code">
            <code>
            {`nc <ip_address> <port>`}
            </code>
        </div>
        <p>This was successful and I received the prompt:</p>
        <img src={image0} alt="a menu screen with options for instructions or to play"/>
        <p>The instructions page looks like:</p>
        <img src={image1} alt="help the robot reach the diamond following the shortest path" />
        <p>And when you choose play:</p>
        <img src={image2} alt="a maze with the robot at the top and a diamond at the bottom" /><br />
        <p>
            When entering the path of the maze if you take too long or are incorrect then the 
            connection is closed.
        </p>
        <p>
            It's a simple case of writing a script that will interact with the server to automatically
            solve the mazes.
        </p><br />
        <p>
            I first wrote a python script that connects to the port and reads off the first maze. I then 
            turns turns the maze into a 2d array which can be searched to solve the maze.
        </p><br />
        <p>
            To solve the maze I decided to use a breadth-first search algorithm, where from the starting
            point in looks at all of the starting points neighbours. It then looks at all of their neighbours
            that haven't already been considered. This continues until the end is found. If the end is not connected
            to the start the maze is impossible and the program exits.
        </p>
        <p>
            To map the path the robot should take at each step of the BFS algorithm, each new position saves which
            neighbour it has come from. This mean that from the end we can look at the step before that, the step
            before the step before that and so on. This continues until we return back to the start.
        </p><br />
        <p>
            All that is required to do now is save this sequence and send it to the server. Then we can receive the 
            next maze and repeat.
        </p><br />
        <p>The full script is below</p><br />
        <DropDown>
            <h4>responder.py</h4>
            <div className="code">
<code className="prettyprint">
{`import socket
import sys

HOST = 'HOST'
PORT = int('PORT')

border = 'ðŸ”¥'
wall = 'â˜ ï¸'
player = 'ðŸ¤–'
path = 'ðŸ”©'
end = 'ðŸ’Ž'

def findStart(grid):
    for x in range(len(grid[1])): # start is always in row 1 of the grid
        if grid[1][x] == player:
            return (x,1)

def checkNeighbor(x, y, parent, visited, toVisit, trace, finished):
    neighbour = (x, y)
    if not neighbour in visited:
        n = grid[y][x]
        if n == path:
            toVisit.append(neighbour)
            visited.append(neighbour)
            trace[neighbour] = parent
        if n == end:
            toVisit = [] # quits loop
            trace[neighbour] = parent
            finished.append(neighbour)

def bfs(grid, start):
    visited = []
    toVisit = [start]
    trace = {}
    finished = []
    while len(toVisit) > 0:
        x, y = toVisit.pop(0)
        for dx in range(-1,2):
            checkNeighbor(x + dx, y, (x, y), visited, toVisit, trace, finished)
        checkNeighbor(x, y + 1, (x, y), visited, toVisit, trace, finished)
    return (trace, finished)


def removeSpaces(s):
    return s.replace(" ", "").replace(u"\ufe0f", "") # removes padding and charater formatting

with socket.socket(socket.AF_INET, socket.SOCK_STREAM) as s:
    s.connect((HOST, PORT))
    # get instructions. these are the same each time but are included for clarity
    data = s.recv(1024)
    print(data.decode(), end="")
    s.sendall(str.encode("1\n"))
    print("1")
    data = s.recv(1024)
    # gets first grid from server
    print(data.decode(), end="")
    s.sendall(str.encode("2\n"))
    print("2")
    data = s.recv(4096)
    print(data.decode(), end="")
    grid = data.decode().strip().split("\n")[:-2]
    grid = list(map(removeSpaces, grid))
    while True: # continue looping till an error is thrown
        start = findStart(grid)
        trace, finished = bfs(grid, start)
        if (len(finished) != 1):
            print("Shouldn't ever happen")
            sys.exit()
        route = ""
        currentNode = finished[0]
        while currentNode != start: # traces from end to start
            cx, cy = currentNode
            px, py = trace[currentNode] # parent node
            direction = ""
            if px - 1 == cx:
                direction = "L"
            if px + 1 == cx:
                direction = "R"
            if py + 1 == cy:
                direction = "D"
            route = direction + route
            currentNode = trace[currentNode]
        s.sendall(str.encode(route + "\n"))
        print(route)
        data = s.recv(100000)
        print(data.decode(), end="")
        grid = data.decode().strip().split("\n")[3:-2]
        grid = list(map(removeSpaces, grid))
`}
</code>
            </div>
        </DropDown>
    </div>
}

export default Component;
